import PropTypes from 'prop-types'
import React from 'react'
import pic03 from '../images/2logo_clube.png'

const Header = props => (
  <header id="header" style={props.timeout ? { display: 'none' } : {}}>
    <div className="logo">
      <span className="icon fa-flask"></span>
    </div>
    <div className="content">
      <div className="inner">
        <span className=" main fit">
          <img style={{ height: 'auto', width: '100%' }} src={pic03} alt="" />
        </span>
        <p></p>
      </div>
    </div>
    <nav class="navbar navbar-expand-lg navbar-light">
      <ul class="navbar-nav">
        <li class="nav-item active">
          <button
            onClick={() => {
              props.onOpenArticle('intro')
            }}
          >
            Sobre
          </button>
        </li>
        <li class="nav-item">
          <button
            onClick={() => {
              props.onOpenArticle('about')
            }}
          >
            Informações
          </button>
        </li>
        {/*  <li class="nav-item">
          <button
            onClick={() => {
              props.onOpenArticle('contact')
            }}
          >
            Inscrição Instituição
          </button>
          </li> */}
        <li class="nav-item dropdown">
          <div class="dropdown">
            <button
              class="btn btn-secondary dropdown-toggle"
              type="button"
              id="dropdownMenu2"
              data-toggle="dropdown"
              aria-haspopup="true"
              aria-expanded="false"
              style={{ 'background-color': '#F08080' }}
            >
              Recursos educacionais abertos
            </button>
            <div class="dropdown-menu" aria-labelledby="dropdownMenuButton">
              <a
                class="dropdown-item"
                onClick={() => {
                  props.onOpenArticle('work')
                }}
                href="#"
              >
                Videos explicativos
              </a>
              <a
              class="dropdown-item"
              onClick={() => {
                props.onOpenArticle('games')
              }}
              href="#"
            >
              Jogos
            </a>
            <a
            class="dropdown-item"
            onClick={() => {
              props.onOpenArticle('animation')
            }}
            href="#"
          >
            Animações
          </a>
              {/*<a class="dropdown-item" href="#">Another action</a>
          <a class="dropdown-item" href="#">Something else here</a>*/}
            </div>
          </div>
        </li>
        <li>
          <button
            onClick={() => {
              props.onOpenArticle('team')
            }}
          >
            Equipe
          </button>
        </li>
        <li>
          <button
            onClick={() => {
              props.onOpenArticle('auth')
            }}
          >
            Direitos Autorais
          </button>
        </li>
      </ul>
    </nav>
  </header>
)

Header.propTypes = {
  onOpenArticle: PropTypes.func,
  timeout: PropTypes.bool,
}

export default Header
